import userEvent from "@testing-library/user-event";
import { tab } from "@testing-library/user-event/dist/tab";
import { useState } from "react"


const Gametab = ({ content }) => {
    const [currentTab, setCurrentTab] = useState(0);

    const TAB = [
        { name: "TOP ", content: "1" },
        { name: "", content: "2" },
        { name: "", content: "3" },
        { name: "", content: "4" },
        { name: "", content: "5" }
    ];
    const TOPGAME = [
        { name: "", company: "Cogonosphere PTE. LTD.", genre: "roleplaying" },
        { name: "PUBG Mobile", company: "Proxima Beta", genre: "action" },
        { name: "", company: "X.D.Global", genre: "roleplaying" },
        { name: "- ", company: "Devsisters corporation", genre: "arcade" },
        { name: "", company: "Yostar Limited.", genre: "strategy" }
    ];

    const selectMenuHandler = (index) => {
        setCurrentTab(index);
    };

    return (
        <section className="Gametab">
            <div className="container">
                <div className="Tab">
                    {TAB.map((element, index) => {
                        return (
                            <div key={index} className={currentTab === index ? "on" : ""} onClick={() => selectMenuHandler(index)}>{element.name}</div>
                        )
                    })}
                </div>
                <div className="Tab_content">
                    <h1>{TOPGAME[currentTab].name}</h1>
                    <p>{TOPGAME[currentTab].company}</p>
                </div>
            </div>
        </section >
    )
}
export default Gametab