import { useEffect, useRef, useState } from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";

const RANK = [
    { id: 1, title: "" },
    { id: 2, title: "M" },
    { id: 3, title: "2" },
    { id: 4, title: "W" },
    { id: 5, title: ":  " },
    { id: 6, title: " " },
    { id: 7, title: " 3rd" },
    { id: 8, title: " " },
    { id: 9, title: "  " }
]

const Rank = ({ content }) => {
    const [IDX, setIDX] = useState();
    useEffect(() => {
        setIDX(0)
    }, []);
    const mainRank = useRef(null);

    const settings = {
        arrows: false,
        autoplay: true,
        autoplaySpeed: 3000,
        afterChange: index => setIDX(index),
        pauseOnHover: false,
        vertical: true,
    }
    return (
        <section className="Rank">
            <strong>   </strong>
            <div className="container">
                <Slider {...settings} ref={mainRank}>
                    {
                        RANK.map((rank, idx) => {
                            return (
                                <div className='rank_slider'>
                                    <ul>
                                        <li key={rank.id} className={"rank0" + rank.id}>{rank.id + ' ' + rank.title}</li>
                                    </ul>
                                </div>
                            )
                        })
                    }
                </Slider>
            </div>
        </section>
    )
}
export default Rank